
#nav {
  position: fixed;
  width: 100%;
  height: 100vh;
  padding-top: 72px;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-content: flex-end;
  text-align: right;
  z-index: 2;
}

#nav h1 {
  color: white;
  text-decoration: none;
  cursor: pointer;
  padding: 9px var(--space) ;
  transition-duration: 0.25s;
}

#nav h1:hover {
  background-color: #1f1f1f;
}

#nav-expand {
  position: fixed;
  width: 72px;
  height: 72px;
  z-index: 3;
  text-align: center;
  border-radius: 100%;
  background-color: white;
}

#nav-expand i {
  line-height: 72px;
  font-size: 18px;
}

#nav-expand i svg {
  transform: translateY(2px);
}